@keyframes heartbeat {
    0% {
      transform: scale(1);
    }
    50% {
      transform: scale(1.3);
    }
    100% {
      transform: scale(1);
    }
  }
  
  .heart-beat {
    color:red;
    display: inline-block;
    animation: heartbeat 2s infinite;
    vertical-align: middle;
  }